.root {
	position: relative;
	display: block;
	-webkit-animation: colorin 1s;
	        animation: colorin 1s;
	margin: 10px auto;
	overflow: hidden;
	width: 290px;
	height: 200px;
	background-color: black;
}

@media (min-width: 480px) {
	.root {
		width: 435px;
		height: 300px;
	}
}

@media (min-width: 680px) {
	.root {
		width: 580px;
		height: 400px;
	}
}

.label {
}

.container {
	overflow: hidden;
	margin: auto;
}

.foo {
	overflow: hidden;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: 0.2s;
	        transition-delay: 0.2s;
	background: black;
}

.fooActive {
	-webkit-transform: translate3d(0,-60px,0);
	        transform: translate3d(0,-60px,0);
}

.figCaption {
	text-align: center;
    position: absolute;
	width: 100%;
    top: auto;
    height: 60px;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: 0.2s;
	        transition-delay: 0.2s;
}

.header {
	color: white;
	text-transform: none;
	line-height: 60px;
}

.figCaptionActive {
	-webkit-transform: translate3d(0,-60px,0);
	        transform: translate3d(0,-60px,0);
}

.image {
	height: 400px;
	width: 580px;
	display: block;
	margin: auto;
}

@-webkit-keyframes colorin {
  from {
	background-color: black;
  }
  to {
	background-color: #df4e4e;
  }
}

@keyframes colorin {
  from {
	background-color: black;
  }
  to {
	background-color: #df4e4e;
  }
}




