.root {
}

.bottom {
	height: 100px;
}

.placeholder {
	position: relative;
	display: block;
	-webkit-animation: fadein 1s;
	        animation: fadein 1s;
	margin: 10px auto;
	overflow: hidden;
	background: black;
	width: 290px;
	height: 200px;
}

@media (min-width: 480px) {
	.placeholder {
		width: 435px;
		height: 300px;
	}
}

@media (min-width: 680px) {
	.placeholder {
		width: 580px;
		height: 400px;
	}
}

@-webkit-keyframes fadein {
  from {
	opacity: 0;
  }
  to {
	opacity: 1;
  }
}

@keyframes fadein {
  from {
	opacity: 0;
  }
  to {
	opacity: 1;
  }
}




